.<template>
  <div class="detail">
      <detail-back></detail-back>
      <detail-header :goods="slide"></detail-header>
      <detail-info ></detail-info>
      <detail-desc :goods="slide"></detail-desc>
      <detail-to-cart></detail-to-cart>
  </div>
</template>

<script>
import axios from 'axios';
import DetailBack from '@/components/detail/back.vue'
import DetailHeader from '@/components/detail/header.vue'
import DetailInfo from '@/components/detail/info.vue'
import DetailDesc from '@/components/detail/desc.vue'
import DetailToCart from '@/components/detail/toCart.vue'
export default {
    name:'Detail',
    components:{
      DetailBack,
      DetailHeader,
      DetailInfo,
      DetailDesc,
      DetailToCart
    },
    data(){
        return{
          scroll: '',
          slide:[]
        }
    },
    methods:{
        getgoodsList(){
            axios({
                method:'get',
                url: 'http://appkr.cn/mock/adbomen.json',
               
            })
            .then(res => {
                let {slide} = res.data.data.abdomen
                this.slide = slide 
            })
            .catch(err => {
                console.error(err); 
            })
        },
      handleScroll(){
            this.scroll  = document.documentElement &&  document.documentElement.scrollTop
            // console.log("scroll")
        }
    },
    mounted(){
        window.addEventListener('scroll',this.handleScroll)
    },
    activated(){
        if(this.scroll > 0){
            window.scrollTo(0,0)
            this.scroll = 0
            window.addEventListener('scroll',this.handleScroll)
        }
    },
    deactivated(){
        window.removeEventListener('scroll',this.handleScroll)
    },
    created(){
        this.getgoodsList()
    }
   
}
</script>

<style lang="stylus" scoped>
.detail
    background #F7F5F5
</style>


