<template>
    <div class="detail-back">

        <router-link tag="div" to="/" class="item-icon back">
            <van-icon name="arrow-left" />
        </router-link>

        <div class="item-icon share ">
            <van-icon name="good-job-o" />
        </div>
    </div>
</template>

<script>
export default {
    name:'DetailBack'
}
</script>

<style lang="stylus" scoped>
.detail-back
    z-index 1
    position fixed
    top 0
    left 0
    height 40px
    width 100%
    display flex
    flex-direction row
    justify-content space-between
    align-items center
    .item-icon
        border-radius 50%
        background rgba(255,255,255,.4)
        height 30px
        line-height 36px
        width 30px
        max-width 30px
        text-align center
        .van-icon
            font-size 18px
    .back
        margin-left 10px
    .share
        margin-right 10px

</style>


