// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

// 全局引入vant-ui
// import Vant from 'vant';
// Vue.use(Vant);

// 按需引入
import { Search, Icon, Col, Row, Swipe, SwipeItem, Grid, GridItem, Image as VanImage, Cell, CellGroup, CountDown,Tab, Tabs,
  GoodsAction, GoodsActionIcon, GoodsActionButton, Tabbar, TabbarItem, Sidebar, SidebarItem,IndexBar, IndexAnchor,Empty,Button
} from 'vant';
import 'vant/lib/index.css';

Vue.use(Search).use(Icon).use(Col).use(Row).use(Swipe).use(SwipeItem).use(Grid).use(GridItem)
  .use(VanImage).use(Cell).use(CellGroup).use(CountDown).use(Tab).use(Tabs).use(GoodsAction)
  .use(GoodsActionIcon).use(GoodsActionButton).use(Tabbar).use(TabbarItem).use(Sidebar).use(SidebarItem)
  .use(IndexBar).use(IndexAnchor).use(Empty).use(Button)



Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



