<template>
    <div class="home">
        <home-header></home-header>
        <home-search></home-search>
        <home-coupons></home-coupons>
        <home-icon :delivery="delivery"></home-icon>
        <home-timelimit></home-timelimit>
        <home-features 
        :newgoods="newgoods" 
        :maygoods="maygoods" 
        :paritygoods="paritygoods" 
        :weekgoods="weekgoods"
        ></home-features>
        <home-recommen :slide="slide" :list="delivery"></home-recommen>
        <tabbar></tabbar>
    </div>
</template>

<script>
import axios from 'axios'
import HomeHeader from "@/components/home/HomeHeader.vue";
import HomeSearch from "@/components/home/HomeSearch.vue";
import HomeCoupons from "@/components/home/HomeCoupons.vue";
import HomeIcon from "@/components/home/HomeIcon.vue";
import HomeTimelimit from "@/components/home/HomeTimelimit.vue";
import HomeFeatures from "@/components/home/HomeFeatures.vue";
import HomeRecommen from "@/components/home/HomeRecommen.vue";
import Tabbar from "@/views/Tabbar.vue";

export default {
    name: "Home",
    data(){
        return{
            delivery:[],
            slide:[],
            delicious:[],
            newgoods:[],
            maygoods:[],
            paritygoods:[],
            weekgoods:[]
        }
    },
    components: {
        Tabbar,
        HomeHeader,
        HomeSearch,
        HomeCoupons,
        HomeIcon,
        HomeTimelimit,
        HomeFeatures,
        HomeRecommen

    },
    methods:{
        getAbdomen(){
            axios({
                method:'get',
                url: 'http://appkr.cn/mock/adbomen.json',
               
            })
            .then(res => {
                let {delivery,slide,delicious,newgoods,maygoods,paritygoods,weekgoods} = res.data.data.abdomen
                this.delivery = delivery
                this.slide = slide
                this.delicious = delicious 
                this.newgoods = newgoods
                this.maygoods = maygoods
                this.paritygoods = paritygoods
                this.weekgoods = weekgoods
            })
        }
         
    },
    mounted(){
        this.getAbdomen()
    }

  

};
</script>

<style lang="stylus" scoped>
.home
    // min-width 650px
    // max-width 750px
    background-color: #FF7547
</style>



