<template>
    <div class="swiper">
        <div ref="wrapper" class="wrapper">
            <ul class="cont" ref="cont">
                <li class="cont-item" v-for="item in recommendList" :key="item.id">
                    <div class="img-box" style="text-align:center">
                        <img class="cont-img" :src="item.img" />
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
    name: 'HomeCoupons',
    data() {
        return {
            recommendList: [
                {
                    id:1,
                    img:require('@/assets/coupons/coupons5.png')
                },
                {
                    id:2,
                    img:require('@/assets/coupons/coupons10.png')
                },
                {
                    id:3,
                    img:require('@/assets/coupons/coupons20.png')
                },
                {
                    id:4,
                    img:require('@/assets/coupons/coupons30.png')
                },
                {
                    id:5,
                    img:require('@/assets/coupons/coupons55.png')
                },
                {
                    id:6,
                    img:require('@/assets/coupons/coupons120.png')
                },
                {
                    id:7,
                    img:require('@/assets/coupons/coupons300.png')
                }
            ]
        }
    },
    methods: {
        verScroll() {
            // 111的数值依据.cont-img 的img宽度＋margin10的总和
            let width = this.recommendList.length * 90 // 动态计算出滚动区域的大小，产生滚动的原因是滚动区域宽度大于父盒子宽度

            this.$refs.cont.style.width = width + 'px' // 修改滚动区域的宽度
            this.$nextTick(() => {
                if (!this.scroll) {
                    this.scroll = new BScroll(this.$refs.wrapper, {
                        startX: 0, //
                        click: true,
                        scrollX: true,
                        scrollY: false,
                        // 关闭滚到头部、尾部回弹动画
                        bounce: false,
                        // 关闭滚动动画效果
                        // momentum:false,
                        
                        eventPassthrough: 'vertical'
                    })
                } else {
                    this.scroll.refresh() //如果dom结构发生改变调用该方法
                }
            })
        },
        handleScroll(){
            this.scroll  = document.documentElement &&  document.documentElement.scrollTop
            console.log("scroll")
        }
    },
    mounted() {
        this.$nextTick(() => {
            let timer = setTimeout(() => {
                if (timer) {
                    // clearTimeout(timer)
                    this.verScroll()
                }
            }, 0)
        })
        window.addEventListener('scroll',this.handleScroll)
    },
    activated(){
        if(this.scroll > 0){
            window.scrollTo(0,0)
            this.scroll = 0
            window.addEventListener('scroll',this.handleScroll)
        }
    },
    deactivated(){
        window.removeEventListener('scroll',this.handleScroll)
    }
}
</script>

<style lang="stylus" scoped>
.swiper
    padding-left 10px
    .wrapper   
        overflow hidden 
        .cont 
            margin 0 auto
            height 100%
            display flex 
            flex-direction row
            align-items center
            .cont-item
                .cont-img 
                    width 80px
                    height 80px
                    margin-right 10px
</style>



