<template>
    <div class="swiper">
        <div ref="wrapper" class="wrapper">
            <ul class="cont" ref="cont" touch-action="none">
                <router-link :to="'/detail/'+item.id" tag="li" class="cont-item" v-for="item in slide" :key="item.id">
                    <div class="cont-img">

                       <van-image width="200px" height="200px" fit="cover" :src="item.url" />
                        <div class="price-info">
                            <p>{{item.name}}{{item.id}}</p>
                        </div>

                        <div class="prices-wrapper">
                            <div class="price-num">
                                <div class="num-box">
                                    <div class="num-item preferential-price">￥{{item.preferential}}</div>
                                    <div class="num-item original-price"><s>￥{{item.original}}</s></div>
                                </div>
                            </div>
                            <div class="addcart-icon">
                                <van-icon name="cart" />
                            </div>

                        </div>
                    </div>
                </router-link>
            </ul>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
    name: 'TimeLimitGoods',
    props:{
        slide:Array
    },

    methods: {

        verScroll() {
            let slideWidth = this.slide.length || 8
            // width的数值依据.cont-img 的img宽度＋margin10的总和
            let width = slideWidth * 210 // 动态计算出滚动区域的大小，产生滚动的原因是滚动区域宽度大于父盒子宽度
            this.$refs.cont.style.width = width + 'px' // 修改滚动区域的宽度
            this.$nextTick(() => {
                if (!this.scroll) {
                    this.scroll = new BScroll(this.$refs.wrapper, {
                        startX: 0, //
                        click: true,
                        scrollX: true,
                        scrollY: false,
                        // 关闭滚到头部、尾部回弹动画
                        bounce: false,
                        eventPassthrough: 'vertical'
                    })
                } else {
                    this.scroll.refresh() //如果dom结构发生改变调用该方法
                }
            })
        }
    },
    mounted() {
        this.$nextTick(() => {
            let timer = setTimeout(() => {
                if (timer) {
                    clearTimeout(timer)
                    this.verScroll()
                }
            }, 0)
        })
    }
}
</script>

<style lang="stylus" scoped>
.swiper
    padding-left .2rem
    .wrapper  
        overflow hidden 
        .cont 
            height 100%
            display flex 
            flex-direction row
            align-items center
            .cont-item
                background #fff
                .cont-img
                    width 200px
                    margin-right 10px
                    .van-image
                        margin-bottom -20px
                    .price-info
                        width 100%
                        height 50px
                        padding 5px
                        font-size 14px
                        line-height 26px
                        margin-bottom 10px
                    .prices-wrapper
                        display flex
                        flex-direction row
                        justify-content space-between
                        align-items center
                        padding-left 10px
                        padding-top 10px

                        .price-num
                            .num-box
                                display flex
                                flex-direction column
                                .num-item
                                    height 20px
                                .preferential-price
                                    font-size 18px
                                    color red
                                .original-price
                                    font-size 14px
                                    color #ccc

                        .addcart-icon
                            width 40px
                            height 40px
                            line-height 46px
                            text-align center
                            color #ffffff
                            background #63D363
                            font-size .5rem
                            border-radius 50%
                            margin-top -30px
                            margin-right 10px

</style>



