<template>
    <div class="coupons">
        <van-row type="flex" justify="space-around">
            <van-col span="6">
                <span class="coup-num">8</span>
                <span class="coup-type">优惠券</span>
            </van-col>
            <van-col span="6">
                <span class="coup-num">100</span>
                <span class="coup-type">积分</span></van-col>
            <van-col span="6">
                <span class="coup-num">0.00</span>
                <span class="coup-type">余额</span>
            </van-col>
        </van-row>
    </div>
</template>

<script>
export default {
    name:'MyCoupons'
}
</script>

<style lang="stylus" scoped>
.coupons
    height 60px
    padding 0 5px
    margin-bottom 10px
    .van-row
        background #fff
        height 100%
        display flex
        align-items center
        border-radius:5px
        .van-col
            display flex
            flex-direction column
            align-items center
            padding 5px
            height 50px
            .coup-num
                font-size 16px
                font-weight bold
                margin-bottom 3px
            .coup-type
                font-size 12px

</style>


