<template>
    <div class="header" ref="header" >
        <div class="good-img"  >
            <van-swipe @change="onChange">
                <van-swipe-item v-for="(item,index) in goods" :key="index" >
                    <img class="img" :src="item.url" />
                </van-swipe-item>
               
                <template #indicator>
                    <div class="custom-indicator" >
                        {{ current + 1 }}
                    </div>
                </template>
            </van-swipe>

        </div>
        <!-- <div v-for="(item,index) in goods" :key="index" > -->
        <div class="good-desc">
            <div class="title">{{this.goods[0].name}}</div>
            <div class="desc-text">{{this.goods[0].description}}</div>
        </div>
        <div class="good-prices">
            <div class="prices-now">￥{{this.goods[0].preferential}}</div>
            <div class="prices-old"><s>￥{{this.goods[0].original}}</s></div>
        </div>
        <div class="aging">
            <div class="ag-item">
                <span class="icon">T</span>
                <span class="ag-text">最快11:00-11:30送达</span>
            </div>
            <div class="ag-item">
                <span class="icon">S</span>
                <span class="ag-text">称重退差价</span>
            </div>
        </div>
        <!-- </div> -->
    </div>
</template>

<script>

export default {
    name: 'DetailHeader',
    props:{
        goods:Array
    },
    data() {
        return {
            current: 0
        };
    },
    methods: {
        onChange(index) {
            this.current = index;
        }
        
    },
    mounted(){
        
      
    }
}
</script>

<style lang="stylus" scoped>
.header 
    overflow hidden
    display flex
    flex-direction column
    justify-content center
    background #ffffff
    margin-bottom 10px
    position relative
    // 图片
    .good-img
        height 0
        padding-bottom 67%
        overflow hidden
        background #333
        .img
            width 100%
        .custom-indicator 
            position: absolute
            right: 5px
            bottom: 5px
            padding: 2px 5px
            font-size: 12px
            background: rgba(0, 0, 0, 0.1)
        

    // 描述
    .good-desc
        padding 10px
        .title
            font-size 16px
            color #000
            margin-bottom 3px
        .desc-text
            font-size 12px
            color #999
    // 价格
    .good-prices
        padding-top 5px
        padding-bottom 10px
        line-height 20px
        display flex
        flex-direction row
        justify-content flex-start
        align-items flex-end 
        margin 0 10px
        border-bottom 1px solid #E2E1E1
        .prices-now
            font-size 16px
            color #ff4400
        .prices-old
            font-size 10px
            color #cccccc
            margin-left 3px
    // 时效
    .aging
        display flex
        flex-direction row
        font-size 9px
        line-height 28px
        padding-left 10px
        .ag-item
            margin-right 10px
</style>



