<template>
    <div class="cate">
        <div class="leftpanels">
            <ul>
                <li v-for="(item,index) in tabsList" @click="selectActive(index)" :class="item.active?'active':''" :key="index" ref="leftitem">
                    {{item.name}}
                </li>
            </ul>
        </div>
        <div class="rightpanels">
            <ul class="goodlist">
                <li class="gooditem" v-for="(item,index) in goodsList" :key="index" >
                    <img :src="item.icon" width="100px">
                    {{item.price}} 
                </li>
            </ul>
        </div>


    <tabbar></tabbar>
    </div>
</template>

<script>
import axios from 'axios'
import Tabbar from "@/views/Tabbar.vue";
export default {
    name:'Category',
    components:{
        Tabbar
    },
    data(){
        return{
            tabsList:[],
            goodsList:[]
        }
    },
    methods:{
        //点击左侧分类
        
        selectActive(index){

            this.tabsList.forEach((val, ind) => {
                if(index == ind){
                    val.active = true
                }else{
                   val.active = false
                }

            })
            this.goodsList = this.tabsList[index].foods
            console.log(this.goodsList)
        },
        async getGoods(index){
            const result = await axios.get('http://appkr.cn/mock/goods.json',{
                params:{
                    type:index
                }
            })
            .then(res => {
                const data = res.data.goods
                this.tabsList = data
            })
            .catch(err => {
                console.error(err); 
            })
        }
    },

    created(){
        this.getGoods()
    },
    mounted(){
        setTimeout(()=>{
            if(this.$refs.leftitem.clicked){
                console.log("sss")
            }
        },0)
    }
}
</script>

<style lang="stylus" scoped>
.cate
    display flex
    .leftpanels
        width 30%
        li
            height 50px
            line-height 50px
            border-bottom 1px solid #fff
            color #333333
            background #f8f8f8
            font-size 14px
        .active
            background #ffffff

    .rightpanels
        flex 1
</style>


