<template>
    <div class="home-features">
        <!-- 楼层标题 -->
        <van-row type="flex" justify="space-between">
            <van-col>
                <div class="line"></div>
                <div class="title">特色专区</div>
            </van-col>
        </van-row>
       
        <!-- 分类商品展示 -->
         <div class="features-wrapper">
            <div class="features-item" >
                <van-cell title="新品尝鲜" size="large" label="五一尝鲜周　新品免费尝" />
                <ul class="features-img">
                    <li class="item"  v-for="item in newgoods" :key="item.id"> <van-image :src="item.url" /></li>
                </ul>
            </div> 

            <div class="features-item">
                <van-cell title="5月爆款" size="large" label="清凉一夏|黄瓜2.99元" />
                <ul class="features-img">
                    <li class="item"  v-for="item in maygoods" :key="item.id"> <van-image :src="item.url" /></li>
                </ul>
            </div> 
            <div class="features-item">
                <van-cell title="平价菜场" size="large" label="专区爆款|买1送1" />
                <ul class="features-img">
                    <li class="item"  v-for="item in paritygoods" :key="item.id"> <van-image :src="item.url" /></li>
                </ul>
            </div> 
            <div class="features-item">
                <van-cell title="一周菜单" size="large" label="初尝夏味鲜" />
                <ul class="features-img">
                    <li class="item"  v-for="item in weekgoods" :key="item.id"> <van-image :src="item.url" /></li>
                </ul>
            </div> 
        </div>
        
    </div>
</template>

<script>
export default {
    name: 'HomeFeatures',
    props:{
        newgoods:Array,
        maygoods:Array,
        paritygoods:Array,
        weekgoods:Array
    }
}
</script>

<style lang="stylus" scoped>
.home-features
    background #ffffff
    line-height 1rem
    
    .van-row
        padding 0 .2rem
    .van-col
        display flex
        flex-direction row
        align-items center

        .line
            width .2rem
            height .8rem
            background green
        .title
            font-size .5rem
            font-weight bold
            margin 0 .2rem
    
    .features-wrapper
        display flex
        flex-wrap wrap
       
        .features-item
            width 50%
            background #ccc
            display flex
            flex-direction column
            overflow hidden
            .van-cell
                height 1.6rem
                color red
                .van-cell__title
                    font-size .35rem
                .van-cell__label
                    font-size .22rem
            .features-img
                width 100%
                display flex
                flex-direction row
                .item
                    height 100px
                    background #fff
                    .van-image
                        // width 120px
                        height 100px
                        padding 1px

</style>



