.<template>
  <div class="desc">
      <div class="desc-img"  v-for="(item,index) in goods" :key="index">
        <img class="cont-img" :src="item.url">
      </div>
  </div>
</template>

<script>
export default {
    name:'DetailDesc',
    props:{
      goods:Array
    }
}
</script>

<style lang="stylus" scoped>
.desc
    overflow hidden
    margin-bottom 10px
    .desc-img
        // width 100%
        padding 0.01px

        .cont-img
            width 100%
           


</style>


