<template>
    <div class="icon">
        <van-grid :border="false" :column-num="5" >
            <router-link tag="van-grid-item" to="/category" v-for="item in delivery" :key="item.id" >
                    <van-image :src="item.url" />
                    <p class=shop-info>{{item.name}}</p>
           </router-link>
        </van-grid>
    </div>
</template>

<script>
export default {
    name: 'HomeIcon',
    props:{
        delivery:Array
    }
}
</script>

<style lang="stylus" scoped>
.icon
    // display block
    // font-size .4rem
    .van-image
        height 100%
        width 100%
        overflow hidden
        border-radius 50%
    .shop-info
        width 100%
        font-size .36rem
        text-align center
        
        
</style>



