<template>
    <div class="header" ref="header">
        <van-row type="flex" justify="space-between">
            <van-col>
                <van-icon name="location-o" />
                <span class="address-item">北京市第三幼儿园</span>
            </van-col>
            <van-col>
                <van-icon class="message-icon" name="bulb-o" badge="9" />
            </van-col>
        </van-row>
    </div>
</template>

<script>

export default {
    name: 'HomeHeader'
    
}
</script>

<style lang="stylus" scoped>

.header 
    line-height 20px 
    padding 15px 20px 5px 10px 
    font-size .36rem 

    .van-col 
        display flex
        flex-direction row 
        align-items center
        .van-icon
            font-size .5rem
</style>



