<template>
    <div class="order">
        <van-cell value="查看全部订单" is-link>
        <template #title>
            <span class="custom-title">我的订单</span>
            <!-- <van-tag type="danger">查看全部订单</van-tag> -->
        </template>
        </van-cell>
        <!-- 每个元素的两侧间隔相等 -->
        <van-row type="flex" justify="space-around">
            <van-col >
                <van-icon name="pending-payment" size="25" />
                <span class="state-name">待支付</span>
            </van-col>
            <van-col >
                <van-icon name="logistics" size="25" />
                <span class="state-name">待收货</span>
            </van-col>
            <van-col >
                <van-icon name="comment-o" size="25" />
                <span class="state-name">待评论</span>
            </van-col>
            <van-col >
                <van-icon name="balance-pay" size="25" />
                <span class="state-name">售后/退款</span>
            </van-col>
        </van-row>
        </div>
    
</template>

<script>
export default {
    name:'MyOrder'
}
</script>

<style lang="stylus" scoped>
.order
    margin-bottom 10px
    padding 0 5px

    
    .van-cell
        font-size 10px
        border-top-left-radius 5px
        border-top-right-radius 5px

    .custom-title
        font-weight bold
        font-size 16px
    .van-row
        background #fff
        height 100px
        display flex
        align-items center
        border-bottom-left-radius 5px
        border-bottom-right-radius 5px
        .van-col
            display flex 
            flex-direction column
            align-items center
            font-size 14px
            color #636363
            .van-icon
                margin-bottom 4px
</style>


