<template>
    <div class="functions">
        <van-cell>
        <template #title>
            <span class="custom-title">常用功能</span>
        </template>
        </van-cell>
        <!-- 每个元素的两侧间隔相等 -->
        <van-row type="flex" justify="space-around">
            <van-col >
                <van-icon name="vip-card-o" size="25" />
                <span class="state-name">绿卡会员</span>
            </van-col>
            <van-col >
                <van-icon name="notes-o" size="25" />
                <span class="state-name">收货地址</span>
            </van-col>
            <van-col >
                <van-icon name="gift-o" size="25" />
                <span class="state-name">邀请有礼</span>
            </van-col>
            <van-col >
                <van-icon name="phone-o" size="25" />
                <span class="state-name">联系客服</span>
            </van-col>
            <van-col >
                <van-icon name="coupon-o" size="25" />
                <span class="state-name">礼品卡</span>
            </van-col>
            <van-col >
                <van-icon name="scan" size="25" />
                <span class="state-name">扫一扫</span>
            </van-col>
            <van-col >
                <van-icon name="records" size="25" />
                <span class="state-name">意见反馈</span>
            </van-col>
            <van-col >
                <van-icon name="setting-o" size="25" />
                <span class="state-name">设置</span>
            </van-col>
        </van-row>
        </div>
    
</template>



<script>
export default {
    name:'MyFunctions'
}
</script>


<style lang="stylus" scoped>
.functions
    margin-bottom 10px
    padding 0 5px

    
    .van-cell
        font-size 10px
        border-top-left-radius 5px
        border-top-right-radius 5px

    .custom-title
        font-weight bold
        font-size 16px
    .van-row
        // padding-top 10px
        background #fff
        display flex
        align-items center
        flex-wrap wrap
        border-bottom-left-radius 5px
        border-bottom-right-radius 5px
        .van-col
            display flex 
            flex-direction column
            align-items center
            font-size 14px
            width 25%
            height 100px
            margin-top 30px
            color #636363
            .van-icon
                margin-bottom 3px
</style>


