<template>
    <div class="login">
        <!-- <img src="" alt=""> -->
        <div class="user-img"><div class="user-icon"><van-icon name="user-o" /></div></div>
        <div class="user-name">
            <span class="user-item name-item">叮咚新用户</span>
            <span class="user-item phone-item"><van-icon name="phone-o" />188xxxx1111</span>
        </div>
    </div>
</template>

<script>
export default {
    name:'MyLogin'
}
</script>

<style lang="stylus" scoped>
.login
    height 80px
    display flex
    background #fff
    padding 5px
    margin-bottom 10px
    .user-img
        width 25%
        display flex
        flex-direction column
        justify-content center
        align-items center
        .user-icon
            width 60px
            height 60px
            background #eee
            display flex
            flex-direction column
            justify-content center
            align-items center
            border-radius 50%
            .van-icon
                text-align center
                font-size 38px
                line-height 65px
                color #999
    .user-name
        flex 1
        padding-top 20px
        display flex
        flex-direction column
        justify-content center
        .user-item
            height 20px
            font-size 16px
            margin-bottom 5px
        .phone-item
            font-size 12px
            color #ccc
            .van-icon
                font-size 10px
</style>


