<template>
  <div class="to-cart">
      <!-- <div class="cart">
          <van-icon name="shopping-cart-o" />
      </div>
      <div class="add-box">
          
      </div> -->
    <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="客服" @click="onClickIcon" />
        <van-goods-action-icon icon="cart-o" text="购物车" @click="onClickIcon" />
        <van-goods-action-button
            type="warning"
            text="加入购物车"
            @click="onClickButton"
        />
        <van-goods-action-button
            type="danger"
            text="立即购买"
            @click="onClickButton"
        />
    </van-goods-action>
  </div>
</template>

<script>
import { Toast } from 'vant';

export default {
    name:'DetailToCart',
  methods: {
    onClickIcon() {
      Toast('联系客服');
    },
    onClickButton() {
      Toast('进入购物车');
    },
  },
};

</script>

<style lang="stylus" scoped>
.to-cart
    z-index 1
</style>


