<template>
    <div class="home-recommen">
        <van-tabs sticky animated>
            
            <van-tab v-for="item in list" :title="item.name" :key="item.id">
                <van-grid square :column-num="2">
                    <van-grid-item v-for="item in slide" :key="item.id" :text="item.name" >
                        
                        <van-image :src="item.url" />

                    </van-grid-item>
                </van-grid>
            </van-tab>
        </van-tabs>

    </div>
</template>

<script>
export default {
    name: 'HomeRecommen',
    props:{
        slide:Array,
        list:Array
    }

}
</script>

<style>

</style>



