<template>
    <div class="info">
        <div class="info-title">
            规格
        </div>
        <ul class="details">
            <li class="item">
                <span class="title">净含量</span>
                <span class="cont">500g</span>
            </li>
             <li class="item">
                <span class="title">保存条件</span>
                <span class="cont">冷藏</span>
            </li>
             <li class="item">
                <span class="title">保质期</span>
                <span class="cont">5天</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'DetailInfo'
}
</script>

<style lang="stylus" scoped>
    .info 
        padding-left 10px
        padding-right 10px
        margin-bottom 10px
        background #ffffff
        .info-title
            line-height 40px
            font-weight bold
            font-size 18px
            border-bottom #ccc solid 1px
        .details
            .item
                line-height 35px
                font-size 14px
                color #797777
                border-bottom #ccc solid 1px
                .title
                    width 70px
                    display inline-block
                    margin-right 20px
                    line-height 35px
            .item:last-child
                border none
                    
</style>


