<template>
    <div class="time-limit" ref="wrapper">
        <!-- 限時搶購 -->
        <van-row type="flex" justify="space-between">
            <van-col>
                <div class="line"></div>
                <div class="title">限时抢购</div>
                <div class="time-back">
                    <van-count-down :time="time">
                        <template v-slot="timeData">
                            <span class="block">{{ timeData.hours }}</span>
                            <span class="colon">:</span>
                            <span class="block">{{ timeData.minutes }}</span>
                            <span class="colon">:</span>
                            <span class="block">{{ timeData.seconds }}</span>
                        </template>
                    </van-count-down>
                </div>
            </van-col>
            <van-col class="more">更多</van-col>
        </van-row>

        <time-limit-goods :slide="slide">
            
        </time-limit-goods>
    </div>
</template>

<script>
import TimeLimitGoods from '@/common/TimeLimitGoods.vue'
export default {
    name: 'HomeTimelimit',
    props:{
      slide:Array
    },
    data() {
        return {
            time: ''
        }
    },
    components: {
        TimeLimitGoods
    },
    methods: {
        getNowTime() {
            let _nowTime = new Date()
            // 获取当前时间
            let nowTime = _nowTime.getTime()

            // 设置当日24点的时间
            let _nightTime = new Date(new Date(new Date().toLocaleDateString()).getTime() + 24 * 60 * 60 * 1000 - 1); // 当天23:59
            // 将24点转换成毫秒数
            let nightTime = _nightTime.getTime()
            // 将时间差（倒计时）传给ｔｉｍｅ
            this.time = nightTime - nowTime
        }

    },
    mounted() {
        this.getNowTime()
    }
}
</script>

<style lang="stylus" scoped>
.time-limit
  background #ffffff
  padding-bottom 10px
  margin-bottom 10px
  border-bottom 1px solid #ccc
  .van-row
    padding .2rem
    .van-col
      display flex
      flex-direction row
      align-items center
      .line
        width .2rem
        height .8rem
        background green
      .title
        font-size .5rem
        font-weight bold
        margin 0 .2rem
      .time-back
        background: #ffffff
        margin-left .3rem
        .colon 
            display: inline-block
            margin: 0 4px
            color: #ffffff
        .block
            display: inline-block
            width: 22px
            color: #fff
            font-size: 12px
            text-align: center
            background: #000000
            border-radius .1rem
    .more
        font-size .4rem
        color green



</style>



